	module.exports = {"data":{"allContentfulRoman":{"edges":[{"node":{"id":"MRuW5XuaIgmmCqIS8kM4o___fr-CA","titreRoman":"Goût du Sang","typeHistoire":"Roman","resume":{"childMarkdownRemark":{"html":"<p>Les péripéthies de Saeko Black, la vampire solitaire. Son histoire se passe 6 ans avant Le Premier Cyborg, mais Saeko n'a physiquement que 19 ans.</p>"}},"slug":"gout-du-sang","chapitreActuel":1,"maximumChapitre":26}},{"node":{"id":"ZDSTPuqlA4406mwOimokS___fr-CA","titreRoman":"Le Premier Cyborg - Tome 1","typeHistoire":"Roman","resume":{"childMarkdownRemark":{"html":"<p>Wamien Mata est le premier être humain à devenir un cyborg suite à un incendie. Il découvrira une chose fabuleuse par la suite, il fait partie des quelques humains à posséder un pouvoir, le sien est le contrôle de la technologie. Il fera la rencontre de plusieurs autres personnes dans son cas et se rendra compte qu'il a un rôle majeur à jouer dans tout ça.</p>"}},"slug":"le-premier-cyborg-tome-1","chapitreActuel":28,"maximumChapitre":28}},{"node":{"id":"c6NPL9f6ImIgwU6MU8AAsug___fr-CA","titreRoman":"Le Premier Cyborg - Tome 2","typeHistoire":"Roman","resume":{"childMarkdownRemark":{"html":"<p>C'est l'histoire de Wamien et de la guilde deux ans après l'enfermement des Black Eagles. Les cas de nouveaux humains sont de plus en plus fréquents et tous ne gardent pas de bonnes intentions pour leurs pouvoirs. La guilde devra donc grandir et former de nouveaux membres pour affronter et neutraliser les nouveaux humains qui nuisent aux simples humains comme vous.</p>"}},"slug":"le-premier-cyborg-tome-2","chapitreActuel":17,"maximumChapitre":26}},{"node":{"id":"c1MpPNcOsLqWmmagQokU2Qq___fr-CA","titreRoman":"Les Red Ravens","typeHistoire":"Roman","resume":{"childMarkdownRemark":{"html":"<p>Après la destruction du SIAM, Daryl a été forcé de travailler pour La Légion. Il a profité de son pouvoir sur la gravité pour s'enfuir et retourner à Damonia. Avec l'aide de Darakei, il a reproduit l'inhibiteur de pouvoirs et part ensuite vers Hutlia où il rencontrera quelques jeunes adultes possédant des pouvoirs et formera avec eux les Red Ravens, groupe de justicier ayant pour but de réduire la criminalité dans les villes d'Hutlia et de Domotress.</p>"}},"slug":"les-red-ravens","chapitreActuel":2,"maximumChapitre":26}},{"node":{"id":"c2pRCdDqp0Y2e0mmkUMoeg2___fr-CA","titreRoman":"La Puissance de la Nature","typeHistoire":"Mini-histoire","resume":{"childMarkdownRemark":{"html":"<p>Gaïa a toujours été une jeune femme qui s'intéressait beaucoup aux plantes et à la nature. Sans grande surprise, son pouvoir se porte sur le contrôle des plantes. </p>"}},"slug":"la-puissance-de-la-nature","chapitreActuel":3,"maximumChapitre":3}},{"node":{"id":"c3ErAMFNOtim4AgiAGkIASk___fr-CA","titreRoman":"Le Défenseur Invisible","typeHistoire":"Mini-histoire","resume":{"childMarkdownRemark":{"html":"<p>Jack Amidus est un jeune homme solitaire et intelligent. Il doit cependant vivre dans une ville où la criminalité est en hausse.</p>"}},"slug":"le-defenseur-invisible","chapitreActuel":3,"maximumChapitre":3}},{"node":{"id":"c6aLAqOgwXCsyCi0Eeg0Ek0___fr-CA","titreRoman":"Le Mastodonte des Black Eagles","typeHistoire":"Mini-histoire","resume":{"childMarkdownRemark":{"html":"<p>Kellan Brogran est le mastodonte de Drake dans les Black Eagles. Mais il n'a pas toujours été un mastodonte, avant il était petit et chétif. Son pouvoir l'a complètement changé.</p>"}},"slug":"le-mastodonte-des-black-eagles","chapitreActuel":3,"maximumChapitre":3}},{"node":{"id":"c2gJx3SJtDacEyK4iOQiOYQ___fr-CA","titreRoman":"Le Monstre de Calpeo","typeHistoire":"Mini-histoire","resume":{"childMarkdownRemark":{"html":"<p>Tim Allam, résidant dans la ville de Calpeo du pays de Mahinu, sera victime d'un accident de voiture. Le jeune homme de taille moyenne et de grosseur moyenne n'aura surprenament rien, dû a son pouvoir de super force qui c'est enclanché sous l'effet de l'adrénalyne.</p>"}},"slug":"le-monstre-de-calpeo","chapitreActuel":3,"maximumChapitre":3}},{"node":{"id":"c3ygs2tqfGMQQIMi8gqgOAA___fr-CA","titreRoman":"Les Ailes de La Guilde","typeHistoire":"Mini-histoire","resume":{"childMarkdownRemark":{"html":"<p>Sam Mithus est un jeune homme sportif et populaire à son école. Il s'implique beaucoup dans la vie étudiante et les sports qui sont pratiqués à son école. Mais pensant avoir une blessure, il arrêtera les sports de l'école et s'embarquera dans le début de sa nouvelle vie de Nouvel Humain.</p>"}},"slug":"les-ailes-de-la-guilde","chapitreActuel":3,"maximumChapitre":3}}]}},"pathContext":{}}

